@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  background-color: var(--dark-main);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --green-main: #0fc684;
  --dark-main: #181f32;
  --white-main: white;
}
a {
  cursor: pointer;
}

.App {
  background-color: #181f32;
  background-color: var(--dark-main);
  height: 100vh;
  font-family: "Montserrat", sans-serif;
  z-index: -2;
  display: flex;
  flex-direction: column;
  align-items: center;
}
/* Navbar */
.Navbar {
  display: flex;
  justify-content: space-between;
  color: white;
  height: 50px;
  position: fixed;
  width: 100%;
  z-index: 11;
  align-items: center;
  background-color: #181f32;
  background-color: var(--dark-main);
}
.download-resume-img:hover {
  stroke: #0fc684;
  stroke: var(--green-main);
}
.navbar__left {
  display: flex;
  width: 100px;
  justify-content: flex-end;
  font-weight: 600;
  font-size: large;
}
.navbar__right {
  display: flex;
  width: 40%;
  justify-content: space-between;
  margin-right: 40px;
}
.navbar__right div {
  padding: 20px;
}
.navbar__right a {
  cursor: pointer;
  /* border-bottom: 1px solid var(--green-main); */
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  color: white;
}
.hamburger-menu {
  display: none;
}
/* Sections */
.section {
  height: 100%;
  width: 90%;
  justify-content: center;
}

/* Home */
.Home {
  background-color: #181f32;
  background-color: var(--dark-main);
  display: flex;
  position: relative;
  z-index: 10;
  width: 100%;
  height: 100%;
}
.home_left {
  height: 100%;
  z-index: 1000;
}
.home__social {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  /* background-color: black; */
  position: fixed;
}
.home__social img {
  width: 35px;
  padding: 20px;
}

.home__right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: white;
  padding: 40px;
}
.home__right__top {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease-in-out;
  margin-bottom: 100px;
}
.home__right a {
  z-index: 10;
}
.masked-image {
  width: 50em;
  transition: all 0.5s ease-in-out;
}
.profile-image {
  width: 15%;
}

.next-page-arrow {
}
/* About */
.About {
  display: flex;
  /* align-items: center; */
  color: white;
  background-color: #181f32;
  background-color: var(--dark-main);
  /* flex-direction: column; */
  justify-content: space-between;
}
.section-right {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  width: 100%;
  padding-top: 100px;
}
.section__title {
  font-size: 1.5em;
  font-weight: 700;
  text-transform: uppercase;
  color: #0fc684;
  color: var(--green-main);
  /* border-bottom: 5px solid var(--green-main); */
}
.section__description {
  width: 60%;
  font-size: 1.6em;
  text-transform: capitalize;
  line-height: 1.3em;

  font-weight: 600;
  border: 5px solid #0fc684;
  border: 5px solid var(--green-main);
  padding: 50px;
  margin-top: 100px;
  border-bottom: 100px solid #0fc684;
  border-bottom: 100px solid var(--green-main);
}
/* active class */
.active {
  color: #0fc684;
  color: var(--green-main);
  font-weight: 600;
  transform: scale(1.3);
  border-bottom: 2px solid #0fc684;
  border-bottom: 2px solid var(--green-main);
}

/* Portfolio */
.Portfolio {
  display: flex;
  color: white;
  background-color: #181f32;
  background-color: var(--dark-main);
  justify-content: space-between;
}
.projects-section {
  display: flex;
  width: 100%;
  flex-direction: column;

  justify-content: center;
  align-items: center;
}
.project {
  display: flex;
  background-color: #0fc684;
  background-color: var(--green-main);
  margin: 20px 10px;
  /* padding: 10px; */
  align-items: center;
  height: 300px;
  padding-left: 100px;
  width: 70%;
}
.project-screenshot {
  padding: 10px;
}
.project-screenshot img {
  border: 2px solid #181f32;
  border: 2px solid var(--dark-main);
  width: 200px;
}
.project-title {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #181f32;
  background-color: var(--dark-main);
  padding: 10px;
  margin-right: 10px;
  font-weight: 700;
  width: 200px;
  text-align: center;
}

.project-description {
  /* display: flex; */
  width: 60%;
  color: #181f32;
  color: var(--dark-main);
  font-size: 1.1em;
  font-weight: 500;
  text-align: left;
}
.link-btn {
  padding: 10px;
  text-decoration: none;
  color: white;
  color: var(--white-main);
  background-color: black;
  padding: 20 60px;
  margin: 10px;
  font-size: 1.2em;
  font-weight: 600;
  opacity: 1;
}
.link-btn:hover {
  /* opacity: 0.8; */
}
.link-demo {
  background-color: #181f32;
  background-color: var(--dark-main);
}
.link-code {
  background-color: crimson;
}
/*  */
.background__image {
  z-index: -11;
  position: absolute;
  height: 100%;
  width: 100%;
  background: url(../../static/media/profile.50013f3e.png);
  background-repeat: no-repeat;
  /* background-position: center; */
  background-size: contain;
  mix-blend-mode: saturation;
  opacity: 0.05;
}

.hide {
  display: none;
}
.section-left {
  width: 75px;
  background-color: black;
}

/* Contact */
.contact__wrapper {
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.contact__icons {
  padding: 100px;
}
.contact__icons img {
  width: 35px;
}
.email {
  font-size: 1.4em;
  color: white;
  font-weight: 500;
}
.contact-item a {
  color: white;
  text-decoration: none;
  font-size: 1.5em;
  font-weight: 600;
  text-transform: lowercase;
  padding: 10px;
}
.contact-item a:hover {
  color: #0fc684;
  color: var(--green-main);
}
@media only screen and (max-width: 1000px) {
  .navbar__right {
    width: 60%;
  }

  .section__description {
    width: 40%;
    font-size: 1.1em;

    margin-top: 10px;
  }
  .project {
    margin: 20px 10px;
    /* padding: 10px; */
    align-items: center;
    height: 300px;
    padding-left: 0px;
    width: 90%;
    flex-direction: column;
    padding-bottom: 50px;
    height: auto;
    /* margin-bottom: 30px; */
  }
  .masked-image {
    width: 40em;
  }
}
.Contact {
  z-index: 10;
}
.contact-item {
  display: flex;
  align-items: center;
  background-color: #181f32;
  background-color: var(--dark-main);
}
@media only screen and (max-width: 800px) {
  .masked-image {
    width: 30em;
  }
}
@media only screen and (max-width: 600px) {
  .home__social img {
    width: 25px;
  }
  .home__left {
    width: 10px;
  }

  .link-btn {
    font-size: 0.7em;
  }
  .Navbar {
    flex-direction: column;
    margin: 0;
    align-items: center;
    justify-content: center;
    height: auto;
    background-color: #181f32;
  }
  .navbar__left {
    justify-content: space-between;
    padding: 0px 30px;
    width: 100vw;
  }

  .navbar__right a {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .hamburger-menu {
    display: block;
    padding: 10px;
    width: 40px;
    cursor: pointer;
  }
  .logo {
    padding: 10px;
  }
  .navbar__right {
    flex-direction: column;
    align-items: center;
    padding: 10px;
    justify-content: center;
  }
  .navbar__right a {
    padding: 20px 0;
  }
  .masked-image {
    width: 20em;
  }
  .text-top {
    font-size: 30px !important;
  }
  .text-bottom {
    font-size: 16px !important;
  }
  .section__description {
    width: 40%;
    font-size: 0.8em;

    margin-top: 10px;
  }
}

.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

